import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import NavHelper from '../../helper/NavHelper';

class Footer extends NavHelper {
    constructor(props) {
        super(props);
        this.state = {
            city_id: "" ,
            cityname:"",
        };
    }

    componentDidMount() {
        const city_id = localStorage.getItem("city_id");
const cityname=localStorage.getItem("selected_city");
        this.setState({ city_id: city_id })
        this.setState({ cityname: cityname })
    }

    render() {
        const backToTop = this.state.stickyHeader ? ' d-block' : '';

        const container_style = {
            padding: "0rem 3rem"
        };

        const mystyle = {
            background: "#ff0000",
            color: "#fff",
            fontWeight: "500",
            border: "none",
            borderRadius: "5px",
            padding: "15px 60px",
            fontFamily: "Futura Md BT"
        };

        return (
            <Fragment>
                {/* Start Footer */}
                <footer className="section-padding footer" style={{ background: "#0e3a54", paddingTop: "3rem", paddingBottom: "2rem" }}>
                    <div className="container-fluid" style={container_style}>
                        <div className="row">
                            <div className="col-lg-3 col-md-6">
                                <div className="footer-box mb-md-40">
                                    <div className="footer_logo mb-4"> <Link to="/"> <img src={process.env.PUBLIC_URL + "/assets/images/logo5.png"} className="img-fluid" alt="logo" style={{ height: "32px" }} /> </Link> </div>
                                    <p className="text-custom-white">
                                        Vroomwheel is India's first online search engine for finding used or second hand Cars.
                                    </p>
                                    <ul className="custom-flex socials">
                                        <li><Link to="#" className="text-custom-white fs-14"><i className="fab fa-facebook-f" /></Link></li>
                                        <li><Link to="#" className="text-custom-white fs-14"><i className="fab fa-twitter" /></Link></li>
                                        <li><Link to="#" className="text-custom-white fs-14"><i className="fab fa-instagram" /></Link></li>
                                        <li><Link to="#" className="text-custom-white fs-14"><i className="fab fa-youtube" /></Link></li>
                                    </ul>
                                    <p className="text-custom-white mt-3"> 2022, VroomWheel | All Right Reserved.</p>
                                </div>
                            </div>
                            <div className="col-lg-3 col-md-6">
                                <div className="footer-box mb-md-40">
                                    <h4 className="text-custom-white fw-600 fot_head">Used Cars</h4>
                                    <ul className="m-0 p-0 main">
                                        <li><Link to="">Bangalore</Link></li>
                                        <li><Link to="">Mumbai</Link></li>
                                        <li><Link to="">Delhi-NCR</Link></li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-lg-3 col-md-6">
                                <div className="footer-box mb-md-40">
                                    <h4 className="text-custom-white fw-600 fot_head">About</h4>
                                    <ul className="m-0 p-0 main">
                                        <li><Link to="">About Us</Link></li>
                                        <li><Link to="">Blog</Link></li>
                                        <li><Link to="">Privacy Policy</Link></li>
                                        <li><Link to="">Terms and Conditions</Link></li>
                                        <li><Link to="">Contact Us</Link></li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-lg-3 col-md-6">
                                <div className="footer-box mb-sm-40">
                                    <h4 className="text-custom-white fw-600 fot_head">Buy Used car in</h4>
                                    <div className="newsletter">
                                        <p className="text-custom-white"><Link to="">Delhi NCR</Link> | <Link to="">Bangalore  </Link> | <Link to="">Mumbai </Link> | <Link to="">Pune </Link> | <Link to="">Delhi </Link> | <Link to="">Noida </Link> | <Link to="">Chennai </Link> | <Link to="">Kolkata </Link> </p>
                                    </div>   
                                    <Link to={"/used-cars-in-"+this.state.cityname} className="btn-first btn-small view_all mb-3" style={mystyle}>Browse Cars</Link>                                                                                                                                                                            
                                </div> 
                            </div>
                        </div>
                    </div>
                </footer>
                <div id="back-top" className={"back-top" + backToTop}> <Link to="#" data-toggle="button" aria-pressed="true" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}><i className="flaticon-arrows"/></Link> </div>
            </Fragment>
        );
    }
}

export default Footer;